		display: flex;
		justify-content: center;
		align-items: center;
		margin: 0px 0px 80px 0px;

		@media (max-width: $breakpoint-medium) {
			margin: 0px 0px 32px 0px;
		}

		@media (max-width: $breakpoint-small) {
			margin: 0px 0px 40px 0px;
		}
	}

	&__inst-profile {
		font-weight: 600;
		font-size: 20px;
		line-height: calc(30/20*100%);
		color: #105393;
	}

	&__slider {
		max-width: 1440px;
		display: flex;
		justify-content: center;
		margin: 0 auto;
	}

	&__item {
		width: 233px;
		height: 233px;

