.meet-with-team-slider {
	background: #105393;
	padding: 0px 0px 120px 0px;

	@media (max-width: $breakpoint-medium) {
		padding: 0px 0px 80px 0px;
	}

	&__wrapper {
		position: relative;
		display: flex;

		& .slick-dots {
			display: flex;
			align-items: center;
			justify-content: center;

			position: absolute;
			left: 50%;
			top: 11.2%;
			width: 100%;
			transform: translate3d(-50%, 0, 0);
			z-index: 1000;

			@media (max-width: $breakpoint-medium) {
				top: 10.5%;
				//width: 100%;
			}

			@media (max-width: $breakpoint-small) {
				top: 13.6%;
				margin-left: 4.5%;
				white-space: nowrap;
				justify-content: start;
			}

			li {
				font-weight: 600;
				font-size: 20px;
				line-height: calc(30/20*100%);
				color: #6F92B4;
				cursor: pointer;
				transition: 0.5s;

				&:not(:last-child) {
					margin: 0px 64px 0px 0px;

					@media (max-width: $breakpoint-medium) {
						margin: 0px 40px 0px 0px;
					}

					@media (max-width: $breakpoint-small) {
						margin: 0px 24px 0px 0px;
					}
				}

				&.slick-active {
					position: relative;
					color: #FFE4BB;

					&::after {
						position: absolute;
						content: '';
						width: 100%;
						height: 4px;
						background: #FFE4BB;
						left: 0;
						bottom: -11px;
					}
				}
			}
		}

		& .slick-arrow {
			position: absolute;
			top: 62.8%;
			//transform: translate3d(0, -50%, 0);
			font-size: 0;
			z-index: 1000;

			@media (max-width: $breakpoint-medium) {
				display: none !important;
			}

			&.slick-prev {
				left: 0;
				background: url("data:image/svg+xml,%3Csvg width='21' height='40' viewBox='0 0 21 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.9167 1L1.375 19.5417L19.9167 38.0833' stroke='%23FFE4BB' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
				width: 21px;
				height: 40px;
				border: none;
				margin: 0px 0px 0px 80px;
			}

			&.slick-next {
				right: 0;
				background: url("data:image/svg+xml,%3Csvg width='21' height='40' viewBox='0 0 21 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 1L19.5417 19.5417L1 38.0833' stroke='%23FFE4BB' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
				border: none;
				width: 21px;
				height: 40px;
				margin: 0px 80px 0px 0px ;
			}
		}
	}

	&__item {
	}

	&__content {

	}
}

