		}

		@media (max-width: $breakpoint-medium) {
			font-size: 48px;
		}

		@media (max-width: $breakpoint-small) {
			width: 80%;
			font-size: 32px;
			line-height: calc(40/32*100%);
			justify-content: center;
		}
	}

	&__subtitle {
		font-weight: 400;
		font-size: 20px;
		line-height: calc(30/20*100%);
		color: #FFE4BB;
		margin: 0px 0px 24.14px 0px;

		@media (max-width: $breakpoint-small) {
			font-size: 16px;
			line-height: calc(24/16*100%);
		}
	}

	&__image {
		position: relative;
		border-radius: 18px;
		margin: 0px 0px 23.86px 0px;

		@media (max-width: $breakpoint-medium) {
			width: auto;
			height: 398px;
		}

		@media (max-width: $breakpoint-small) {
			width: auto;
			height: 220px;
			border-radius: 7px;
			object-fit: cover;
		}
	}

	&__button {
		cursor: pointer;
		width: 220px;
		padding: 16px 0px;
		background: #FF8F8F;
		border-radius: 38px;
		text-align: center;
		transition: 0.5s;
		margin: 0 auto;

		&:hover {
			background: #E86E6E;
		}

		@media (max-width: $breakpoint-small) {
			padding: 12px 0px;
		}
	}

	&__link {
		font-weight: 600;
		font-size: 16px;
		line-height: calc(24/16*100%);
		letter-spacing: 0.04em;
		color: #FFFFFF;
	}
}

