.bestsellers-slider {
	&__wrapper {
		position: relative;
		display: flex;

		& .slick-arrow {
			display: none;
			font-size: 0;
		}

		& .slick-dots {
			display: flex;
			align-items: center;
			justify-content: center;

			position: absolute;
			left: 50%;
			bottom: 0;
			transform: translate3d(-50%, -6%, 0);

			li {
				list-style: none;
				margin: 0px 4px 113px 4px;

				@media (max-width: $breakpoint-medium) {
					margin: 0px 4px 74px 4px;
				}

				&.slick-active button{
					background: #FF8F8F;
					opacity: 1;
				}
			}

			button {
				font-size: 0;
				width: 15px;
				height: 15px;
				background: #FF8F8F;
				opacity: .3;
				border: none;
				border-radius: 50%;

				@media (max-width: $breakpoint-medium) {
					padding: 0;
					width: 8px;
					height: 8px;
				}
			}
		}
	}

	&__title {
		text-align: center;
		font-family: 'DM Serif Display';
		font-weight: 400;
		font-size: 64px;
		line-height: calc(88/64*100%);
		color: #E86E6E;
		margin: 0px 0px 48px 0px;

		@media (max-width: $breakpoint-medium) {
			font-size: 48px;
			line-height: calc(65.81/48*100%);
			margin: 0px 0px 38px 0px;
		}

		@media (max-width: $breakpoint-small) {
			font-size: 32px;
			line-height: calc(40/32*100%);
			margin: 0px 0px 24px 0px;
		}
	}

	&__item {
	}

	&__content {
		//position: relative;
		text-align: center;
	}
}

.content-bs {
	margin-bottom: 183px;

	@media (max-width: $breakpoint-medium) {
		margin-bottom: 128px;
	}

	&__block-image {
		position: relative;
	}

	&__image {
		margin: 0px 0px 8px 0px;

		//@media (max-width: 1351px) {
		//	width: 291px;
		//}
	}

	&__title {
		font-weight: 400;
		font-size: 16px;
		line-height: calc(24/16*100%);
		color: #414249;
		margin: 0px 0px 8px 0px;
	}

	&__price {
		font-weight: 600;
		font-size: 16px;
		line-height: calc(24/16*100%);
		color: #414249;
		margin: 0px 0px 16px 0px;
	}

	&__button {
		display: flex;
		width: 180px;
		height: 48px;
		padding: 12px 33px;
		margin: 0 auto;
		border: 2px solid #FF8F8F;
		border-radius: 38px;
		align-items: center;

		&:hover {
			transition: 0.5s;
			background: #FF8F8F;

			.content-bs__link {
				color: #fff;
			}
		}
	}

	&__link {
		font-weight: 600;
		font-size: 16px;
		line-height: calc(24/16*100%);
		letter-spacing: 0.04em;
		text-transform: uppercase;
		color: #FF8F8F;
		white-space: nowrap;
		transition: 0.5s;
	}
}

