.favorite-books-slider {

	&__wrapper {
		position: relative;
		display: flex;

		& .slick-arrow {
			position: absolute;
			top: 50%;
			transform: translate3d(0, -50%, 0);
			font-size: 0;
			z-index: 20;

			&.slick-disabled {
				display: none !important;
			}

			&.slick-prev {
				left: 0;
				background: url("data:image/svg+xml,%3Csvg width='21' height='40' viewBox='0 0 21 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M19.9167 1.31531L1.375 19.857L19.9167 38.3986' stroke='%23FFE4BB' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
				width: 21px;
				height: 40px;
				border: none;
				margin: 0px 0px 0px 80px;

				@media (max-width: $breakpoint-medium) {
					left: 0%;
					background: url("data:image/svg+xml,%3Csvg width='11' height='20' viewBox='0 0 11 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M9.33333 1.66669L1 10L9.33333 18.3334' stroke='%23105393' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
					width: 10px;
					height: 20px;
					border: none;
					margin: 0px 0px 0px 30px;
				}

				@media (max-width: $breakpoint-small) {
					left: -25px;
					top: 47%;
					background: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.33334 1.33333L8 8L1.33334 14.6667' stroke='%23105393' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") no-repeat;
					//background-repeat: no-repeat;
					background-position: center;
					width: 32px;
					height: 32px;
					background-color: #fff;
					border-radius: 50%;
					transform: rotate(180deg);
					border: none;
					margin: 0px 0px 0px 30px;
				}
			}

			&.slick-next {
				right: 0;
				background: url("data:image/svg+xml,%3Csvg width='21' height='40' viewBox='0 0 21 40' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.0835 1.31531L19.6252 19.857L1.0835 38.3986' stroke='%23105393' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
				border: none;
				width: 21px;
				height: 40px;
				margin: 0px 80px 0px 0px ;

				@media (max-width: $breakpoint-medium) {
					right: 0%;
					background: url("data:image/svg+xml,%3Csvg width='11' height='20' viewBox='0 0 11 20' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.66667 1.66669L10 10L1.66667 18.3334' stroke='%23105393' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
					border: none;
					width: 10px;
					height: 20px;
					margin: 0px 30px 0px 0px ;
				}

				@media (max-width: $breakpoint-small) {
					right: -25px;
					top: 49.4%;
					background: url("data:image/svg+xml,%3Csvg width='9' height='16' viewBox='0 0 9 16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.33334 1.33333L8 8L1.33334 14.6667' stroke='%23105393' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A") no-repeat;
					background-repeat: no-repeat;
					background-position: center;
					border: none;
					width: 32px;
					height: 32px;
					background-color: #fff;
					border-radius: 50%;
					margin: 0px 30px 0px 0px;
				}
			}
		}
	}

	&__item {
		flex: 0 1 50%;

		&_pink {
			background: #FF8F8F;
		}

		&_blue {
			background: #CEE8FF;
		}
	}

	&__content {
		padding: 120px 0px 130px 0px;
		text-align: center;

		@media (max-width: $breakpoint-medium) {
			padding: 80px 0px 80px 0px;
		}

		@media (max-width: $breakpoint-small) {
			padding: 50px 0px 72px 0px;
		}
	}
}

.content-fb {
	&__image {
		&_bork {
			margin: 0px 0px 54.15px 0px;

			@media (max-width: $breakpoint-medium) {
				width: 270px;
				height: auto;

				margin: 0px 0px 40px 0px;
			}

			@media (max-width: $breakpoint-small) {
				margin: 0px 0px 24px 0px;
			}
		}

		&_rendel {
			margin: 0px 0px 39.78px 0px;

			@media (max-width: $breakpoint-medium) {
				width: 257px;
				height: auto;
			}

			@media (max-width: $breakpoint-small) {
				margin: 0px 0px 24px 0px;
			}
		}
	}

	&__subtitle {
		font-weight: 600;
		font-size: 20px;
		line-height: calc(30/20*100%);
		color: #fff;
		margin: 0px 0px 19px 0px;

		@media (max-width: $breakpoint-medium) {
			margin: 0px 0px 11px 0px;
		}
	}

	&__rating {
		margin: 0px 0px 16px 0px;

		@media (max-width: $breakpoint-medium) {
			margin: 0px 0px 10px 0px;
		}
	}

	&__title {
		font-family: 'DM Serif Display';
		font-weight: 400;
		font-size: 48px;
		line-height: calc(56/48*100%);
		margin: 0px 0px 16px 0px;

		@media (max-width: $breakpoint-medium) {
			font-size: 24px;
			line-height: calc(32.9/24*100%);
			margin: 0px 0px 8px 0px;
		}
	}

	&-white-color {
		color: #fff;
	}

	&-blue-color {
		color: #105393;
	}

	&__text {
		font-weight: 400;
		font-size: 16px;
		line-height: calc(24/16*100%);

		margin: 0 auto;

		@media (max-width: $breakpoint-medium) {
			font-size: 14px;
			line-height: calc(21/14*100%);
			width: 90%;
		}

		> br {
			@media (max-width: $breakpoint-medium) {
				display: none;
			}
		}
	}
}



.icon-fb-star {
	color: #FFE4BB;
	font-size: 19.5px;
}

.icon-fb-star_checked {
	color: #F2994A;
}




