			width: 47px;
			height: 45px;
			z-index: 20;
			top: 23%;
			right: 21%;

			@media (max-width: $breakpoint-medium) {
				top: 13%;
				right: 10.5%;
			}

			@media (max-width: $breakpoint-small) {
				top: 15%;
				right: 12%;
			}
		}

