			width: 105px;
			height: 100px;
			z-index: 20;
			bottom: 20%;
			right: 10%;

			@media (max-width: $breakpoint-medium) {
				width: 0;
				height: 0;
			}
		}
	}

	&__content {
		//position: absolute;
		text-align: center;
		margin-top: 15px;

		@media (max-width: $breakpoint-small) {
			margin-top: 160px;
		}
	}

	&__title {
		position: relative;
		font-family: 'DM Serif Display';
		font-weight: 400;
		font-size: 64px;
		line-height: calc(88/64*100%);
		color: #fff;
		margin: 0px 0px 24px 0px;

		& .icon-title::after {
			@media (max-width: $breakpoint-medium) {
				background-image: url("data:image/svg+xml,%3Csvg width='49' height='41' viewBox='0 0 49 41' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.6758 2.94236C23.1282 1.32816 20.2616 1.73421 20.5692 3.5252C21.4345 8.57362 22.3188 14.8334 24.7996 19.3631C25.5314 20.6998 27.4413 19.7884 27.446 18.5048C27.4785 13.5116 25.277 7.63976 23.6758 2.94236Z' fill='%23FF8F8F'/%3E%3Cpath d='M5.46538 17.7842C3.82194 17.364 2.64554 19.9569 4.31024 20.6714C9.00181 22.6868 14.7192 25.3421 19.8306 25.7395C21.3388 25.857 21.5851 23.7827 20.5112 23.0842C16.3411 20.3554 10.2519 19.0005 5.46538 17.7842Z' fill='%23FF8F8F'/%3E%3Cpath d='M14.9778 33.0534C11.37 33.8109 8.01966 35.4005 4.63963 36.8256C2.83532 37.5895 3.912 40.2942 5.75622 39.6614C9.2303 38.4751 12.7716 37.4149 15.9703 35.574C17.1594 34.896 16.2886 32.7736 14.9778 33.0534Z' fill='%23FF8F8F'/%3E%3Cpath d='M43.7153 8.96532C40.2732 12.0705 37.0815 15.4347 33.5463 18.4467C31.9634 19.794 33.6111 22.5014 35.3946 21.219C39.4909 18.2616 43.2737 14.8133 45.9437 10.4884C46.8167 9.06782 44.7639 8.01641 43.7153 8.96532Z' fill='%23FF8F8F'/%3E%3C/svg%3E%0A");
				width: 46px;
				height: 40px;
				top: -55px;
				left: -28px;
			}

			@media (max-width: $breakpoint-small) {
				background-image: url("data:image/svg+xml,%3Csvg width='48' height='42' viewBox='0 0 48 42' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M23.1856 3.53172C22.638 1.91752 19.7714 2.32356 20.0789 4.11456C20.9443 9.16298 21.8286 15.4227 24.3094 19.9525C25.0412 21.2891 26.951 20.3777 26.9558 19.0941C26.9883 14.101 24.7867 8.22912 23.1856 3.53172Z' fill='%23FF8F8F'/%3E%3Cpath d='M4.97511 18.3735C3.33167 17.9534 2.15528 20.5462 3.81998 21.2608C8.51154 23.2762 14.229 25.9315 19.3404 26.3289C20.8486 26.4463 21.0949 24.3721 20.0209 23.6736C15.8508 20.9447 9.76166 19.5899 4.97511 18.3735Z' fill='%23FF8F8F'/%3E%3Cpath d='M14.4876 33.6428C10.8798 34.4002 7.52945 35.9899 4.14943 37.415C2.34512 38.1789 3.4218 40.8835 5.26602 40.2508C8.7401 39.0645 12.2814 38.0043 15.4801 36.1634C16.6692 35.4853 15.7984 33.363 14.4876 33.6428Z' fill='%23FF8F8F'/%3E%3Cpath d='M43.2251 9.55467C39.7829 12.6598 36.5912 16.0241 33.056 19.0361C31.4732 20.3834 33.1208 23.0907 34.9044 21.8083C39.0006 18.851 42.7834 15.4027 45.4534 11.0777C46.3265 9.65718 44.2736 8.60577 43.2251 9.55467Z' fill='%23FF8F8F'/%3E%3C/svg%3E%0A");
				top: -45px;
				left: -27px;
			}
		}

		@media (max-width: $breakpoint-medium) {
			font-size: 48px;
			margin: 0px 0px 16px 0px;
		}

		@media (max-width: $breakpoint-small) {
			font-size: 32px;
			line-height: calc(40/32*100%);
			margin: 0px auto 24px auto;
			width: 80%;
			//justify-content: center;
		}
	}

	.title-with-icon_l_ms::after {
		@media (max-width: $breakpoint-small) {
			position: absolute;
			content: '';
			height: 43px;

			left: -8%;
			top: -27%;
		}
	}

	&__buttons {
		display: flex;
		justify-content: center;
	}

	&__button {
		min-width: 180px;
		border-radius: 38px;
		padding: 16px 0px;
		cursor: pointer;

		@media (max-width: $breakpoint-small) {
			min-width: 140px;
			padding: 12px 0px;
		}
	}

	&__button:not(:last-child) {
		margin: 0px 32px 0px 0px;

		@media (max-width: $breakpoint-medium) {
			margin: 0px 24px 0px 0px;
		}

		@media (max-width: $breakpoint-small) {
			margin: 0px 10px 0px 0px;
		}
	}

	&__button_pink {
		background: #FF8F8F;
	}

	&__button_pink > a{
		color: #fff;
	}

	&__button_pink:hover {
		background: #E86E6E;
		transition: 0.5s;
	}

	&__button_yellow {
		background: #FFE4BB;
	}

	&__button_yellow:hover {
		background: #FFDCA7;
		transition: 0.5s;
	}

	&__button_yellow > a {
		color: #E86E6E;
	}

	&__link {
		font-weight: 600;
		font-size: 16px;
		line-height: calc(24/16*100%);
		letter-spacing: 0.04em;
		text-transform: uppercase;
	}

	&__image {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		z-index: -1;
	}

	& .slick-arrow {
		position: absolute;
		top: 50%;
		transform: translate3d(0, -50%, 0);
		z-index: 10;
		font-size: 0;
		width: 14px;
		height: 72px;

		@media (max-width: $breakpoint-medium) {
			display: none !important;
		}

		&.slick-prev {
			left: 0;
			background: url("data:image/svg+xml,%3Csvg width='14' height='22' viewBox='0 0 14 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M12.8107 0.43934C13.3964 1.02513 13.3964 1.97487 12.8107 2.56066L4.37132 11L12.8107 19.4393C13.3964 20.0251 13.3964 20.9749 12.8107 21.5607C12.2249 22.1464 11.2751 22.1464 10.6893 21.5607L1.18934 12.0607C0.603553 11.4749 0.603553 10.5251 1.18934 9.93934L10.6893 0.43934C11.2751 -0.146447 12.2249 -0.146447 12.8107 0.43934Z' fill='white'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
			border: none;
			margin: 0px 0px 0px 45px;
		}

		&.slick-next {
			right: 0;
			background: url("data:image/svg+xml,%3Csvg width='14' height='22' viewBox='0 0 14 22' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M1.18934 0.43934C1.77513 -0.146447 2.72487 -0.146447 3.31066 0.43934L12.8107 9.93934C13.3964 10.5251 13.3964 11.4749 12.8107 12.0607L3.31066 21.5607C2.72487 22.1464 1.77513 22.1464 1.18934 21.5607C0.603553 20.9749 0.603553 20.0251 1.18934 19.4393L9.62868 11L1.18934 2.56066C0.603553 1.97487 0.603553 1.02513 1.18934 0.43934Z' fill='white'/%3E%3C/svg%3E%0A") 0 0 / 100% no-repeat;
			border: none;
			margin: 0px 45px 0px 0px ;
		}
	}

	& .slick-dots {
		display: flex;
		align-items: center;
		justify-content: center;

		position: absolute;
		left: 50%;
		bottom: 6%;
		transform: translate3d(-50%, -6%, 0);

		@media (max-width: $breakpoint-medium) {
			bottom: 11%;
		}

		@media (max-width: $breakpoint-small) {
			bottom: 8%;
		}

		li {
			list-style: none;
			margin: 0 4px;

			&.slick-active button{
				background: #FF8F8F;
				opacity: 1;
			}
		}

		button {
			font-size: 0;
			width: 15px;
			height: 15px;
			background: #fff;
			opacity: .7;
			border: none;
			border-radius: 50%;

			@media (max-width: $breakpoint-medium) {
				width: 8px;
				height: 8px;
				padding: 0;
			}
		}
	}
}

.slick-list {
	overflow: hidden;
}

.slick-track {
	display: flex;
}
